import React from 'react';
import Link from 'gatsby-link';
import vwEmailersBanner from "./static/images/vwEmailersBanner.png";
import vwEmailer1 from "./static/images/vwEmailer1.jpg";
import vwEmailer2 from "./static/images/vwEmailer2.jpg";
import vwEmailer3 from "./static/images/vwEmailer3.jpg";
import vwEmailer4 from "./static/images/vwEmailer4.jpg";
import vwEmailer5 from "./static/images/vwEmailer5.jpg";
import vwEmailer6 from "./static/images/vwEmailer6.jpg";
import supersonicImg from "./static/images/supersonicImg.jpg";
import ebayImg from "./static/images/ebayImg.jpg";

const vwemailers = () => (
  <div className="fullBox">
    <div className="insideBanner vwEmailerBanner">
      <div className="bg vwEmailer"></div>
      <div className="midWrapper">
            <div className="rightSection">
              <div className="box">
                <div className="vMid">
                  <img src={vwEmailersBanner} alt="" />
                </div>
              </div>
            </div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h1>DIGITAL<br />MARKETING</h1>
                  <h2><span>MAILERS</span></h2>
                  <p>Client : Volkswagen India<br />Work : Emailers<br />Agency : 22feet Tribal Worldwide </p>
                </div>
              </div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="vwEmailerContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="two">
            <li><img src={vwEmailer1} alt="" /> <img src={vwEmailer3} alt="" /><img src={vwEmailer5} alt="" /></li>
            <li><img src={vwEmailer2} alt="" /> <img src={vwEmailer4} alt="" /><img src={vwEmailer6} alt="" className="lastImg" /></li>
          </ul>
        </div>
      </div>
  </div>

  <div className="midWrapper nextPrevious textAlignCenter">
    <ul>
      <li className="back">
        <Link to="/vh1supersonic">
          <div className="thumb"> <img src={supersonicImg} alt="" /> </div>
          <div className="button"><span>Back</span></div>
        </Link>
      </li>
      <li className="next">
        <Link to="/ebayindia">
          <div className="thumb"> <img src={ebayImg} alt="" /> </div>
          <div className="button"><span>next</span></div>
        </Link>
      </li>
    </ul>
  </div>

  </div>
)

export default vwemailers



